import { useEffect, useState } from 'react';
import { db } from './../../services/firebase';
import { getDoc, doc, setDoc } from '@firebase/firestore';
import { useWindowWidth } from '../../hooks/useWindowWidth';
export function Settings() {
	const [volume, setVolume] = useState(0);
	const { windowWidth } = useWindowWidth();
	const volumeRef = doc(db, 'settings', 'volume');

	const getVolumeFromDB = async () => {
		await getDoc(volumeRef).then((snapshot) => {
			const data = snapshot.data().volume;
			setVolume(data);
		});
	};

	useEffect(() => {
		getVolumeFromDB();
	}, []);

	const handleInputVolume = ({ target: { value } }) => {
		setVolume(value);
	};
	const setVolumeToDB = async (e) => {
		e.preventDefault();
		console.log(volume);
		setDoc(volumeRef, { volume: volume });
	};

	return (
		<section className="container-sm py-5 px-3">
			<h2 className="mb-5">Settings</h2>

			<form className="d-flex flex-column">
				<label htmlFor="volume" className="fs-5">
					underline positions in red if there is less volume left than:
				</label>

				<div
					className={` d-flex gap-3  ${
						windowWidth < 576 ? 'flex-column row-gap-3' : 'flex-row '
					} mt-3 w-50`}
				>
					<div className="col-auto ">
						<input
							className="form-control me-2 "
							type="number"
							step="any"
							name="volume"
							placeholder="Enter the volume"
							value={volume}
							onChange={handleInputVolume}
						/>
					</div>
					<div className="col-auto me-auto ">
						<button
							className="btn btn-outline-success "
							type="submit"
							onClick={setVolumeToDB}
						>
							Submit
						</button>
					</div>
				</div>
			</form>
		</section>
	);
}
