import PropTypes from 'prop-types';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { useRef } from 'react';
export function Bottle({ id, name, volume, onClickDelete }) {
	const parentElementRef = useRef(null);
	const removeBottleFromCollection = async () => {
		try {
			await onClickDelete(parentElementRef.current.id);
		} catch (error) {
			console.log(error.message);
		}
	};

	const stylesLiBottle = {
		flex: '1 0 50%',
		display: 'flex',
		justifyContent: 'space-between',
		fontSize: '1.5rem',
	};
	return (
		<li
			ref={parentElementRef}
			id={id}
			className={`bottle list-group-item d-flex justify-content-between align-items-center gap-3 ${
				volume <= 0.5 ? 'text-bg-warning' : ''
			}`}
		>
			<div style={stylesLiBottle}>
				<span className="bottle__name col-6 fs-5 w-auto">{name}</span>

				<span className="bottle__volume badge bg-primary rounded col-2 align-self-center w-auto">
					{volume}
				</span>
			</div>

			<div className="col-auto d-flex gap-2">
				<button className="btn btn-outline-dark col-auto w-auto ">
					<i className="bi bi-eye"></i>
				</button>
				<button
					className="btn btn-outline-dark col-auto w-auto"
					onClick={removeBottleFromCollection}
				>
					<i className="bi bi-trash"></i>
				</button>
			</div>
		</li>
	);
}
Bottle.propTypes = {
	id: PropTypes.string,
	name: PropTypes.string,
	volume: PropTypes.number,
	onClickDelete: PropTypes.func,
};
