import { db } from './../../services/firebase';
import { useState } from 'react';
import { useBottles } from './../../hooks/useBottles';
import { doc, deleteDoc } from 'firebase/firestore';
import { Bottle } from 'components/bottle/Bottle';
import { Modal } from 'components/modal/Modal';
import './MyBeverages.css';

export function MyBeverages() {
	const { bottles } = useBottles();
	const [typeOfBeverage, setTypeOfBeverage] = useState('All');

	const handleInputKindOfBottle = ({ target: { value } }) => {
		setTypeOfBeverage(value);
	};

	const filterBevs = (option) => {
		const filteredBevs = bottles.filter((bottle) => {
			switch (option) {
				case 'All':
					return bottle;

				case 'Alcoholic':
					return bottle.isAlcoholic;
				case 'N/A':
					return !bottle.isAlcoholic;
				default:
					return bottle;
			}
		});
		return filteredBevs;
	};

	const filteredBevs = filterBevs(typeOfBeverage);

	const removeBottleFromCollection = async (id) => {
		await deleteDoc(doc(db, 'bar', id));
	};

	return (
		<section
			className="myBeverages p-5 d-flex flex-column gap-4"
			id="myBeverages"
		>
			<h2>My beverages</h2>
			<h4>Now you have {bottles.length} beverages in your bar.</h4>
			<div>
				<Modal />
			</div>
			<div
				className="btn-group"
				role="group"
				aria-label="Basic radio toggle button group"
			>
				<input
					type="radio"
					className="btn-check"
					name="type"
					id="all"
					value="All"
					autoComplete="off"
					checked={typeOfBeverage === 'All'}
					onChange={handleInputKindOfBottle}
				/>
				<label className="btn btn-outline-primary" htmlFor="all">
					All
					<span>{` (${bottles.length})`}</span>
				</label>

				<input
					type="radio"
					className="btn-check"
					name="type"
					id="alco"
					value="Alcoholic"
					autoComplete="off"
					checked={typeOfBeverage === 'Alcoholic'}
					onChange={handleInputKindOfBottle}
				/>
				<label className="btn btn-outline-primary" htmlFor="alco">
					Alcoholic
					<span>{` (${
						bottles.filter((bottle) => bottle.isAlcoholic).length
					})`}</span>
				</label>

				<input
					type="radio"
					className="btn-check"
					name="type"
					id="non-alco"
					value="N/A"
					autoComplete="off"
					checked={typeOfBeverage === 'N/A'}
					onChange={handleInputKindOfBottle}
				/>
				<label className="btn btn-outline-primary" htmlFor="non-alco">
					N/A
					<span>{` (${
						bottles.filter((bottle) => !bottle.isAlcoholic).length
					})`}</span>
				</label>
			</div>
			<ul className="list-group">
				{filteredBevs.map((bottle) => (
					<Bottle
						key={bottle?.id}
						id={bottle?.id}
						name={bottle?.name}
						volume={bottle?.volume}
						onClickDelete={removeBottleFromCollection}
					/>
				))}
			</ul>
		</section>
	);
}
