import { HashRouter, Routes, Route } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.js';
import {
	Layout,
	HomePage,
	OutOfStockPage,
	SettingsPage,
	NotFoundPage,
} from './routes';
import './App.css';
import { db } from './services/firebase';
import { collection, onSnapshot } from 'firebase/firestore';
import { BottlesProvider } from './hooks/useBottles';
import { useState, useEffect, useRef } from 'react';

function App() {
	const [bottles, setBottles] = useState([]);
	const bottlesRef = useRef(bottles);
	useEffect(() => {
		const unsub = onSnapshot(collection(db, 'bar'), (snapshot) => {
			bottlesRef.current = snapshot.docs.map((doc) => doc.data());
			setBottles(bottlesRef.current);
		});

		return () => unsub();
	}, []);
	return (
		<BottlesProvider value={{ bottles, setBottles: (i) => setBottles(i) }}>
			<HashRouter>
				<Routes>
					<Route path="/" element={<Layout />}>
						<Route index element={<HomePage />} />
						<Route path="out-of-stock" element={<OutOfStockPage />} />
						<Route path="settings" element={<SettingsPage />} />
						<Route path="*" element={<NotFoundPage />} />
					</Route>
				</Routes>
			</HashRouter>
		</BottlesProvider>
	);
}

export default App;
